
ul,li,div,p {
  padding:0;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.headline {
  display:block;
  margin:0 auto;
  text-align: center;
  font-size: 45px;
  font-weight: 900;
}

.container {
  display: block;
  min-height:100vh;
  margin: 0 auto;
  padding: 20px;
  background-image: url('./old_paper.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  border:ridge 10px goldenrod;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
